import { Component, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
import { GhostService } from './../../providers/ghost/ghost.service';

@Component({
    selector: 'cs-ghost',
    host: { '[class.isLoading]': 'isLoading' },
    template: `<ng-content *ngIf="!isLoading"></ng-content>`
})
export class GhostComponent {
    @Input() group: any = null;

    public isLoading: boolean = true;
    private isLoadingSubscription: Subscription;

    private data: Object = {};
    public constructor(
        private _ghostSrv: GhostService
    ) {
        this.isLoadingSubscription = this._ghostSrv.isLoading.subscribe(data => this._updateLoading(data));
    }

    public ngOnDestroy() {
        this.isLoadingSubscription.unsubscribe();
    }

    private _updateLoading(data) {
        if (!data) return
        this._setData(data)._process();
    }

    private _setData(data: Object) {
        this.data = data;
        return this;
    }

    private _process() {
        return this.group ? this._groupedLoading() : this._setLoading();
    }

    private _groupedLoading() {
        return Array.isArray(this.data['group']) ? this._groupedByArray() : this._groupedByString();
    }

    private _groupedByArray() {
        if (this.data['group'].indexOf(this.group) > -1) return this._setLoading();

    }

    private _groupedByString() {
        if (this.data['group'] === this.group) return this._setLoading();
    }

    private _setLoading() {
        this.isLoading = this.data['isLoading']
    }
}


